<template>
  <div class="add-blog">
    <h2>添加博客</h2>
    <form action="" v-if="submmited">
      <label>博客标题</label>
      <input type="text" v-model="blog.title" required>
      <label>博客内容</label>
      <textarea v-model="blog.content"></textarea>

<!--      分类-->
      <div id="checkboxes">
        <label>Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categories">
        <label>Node.js</label>
        <input type="checkbox" value="Node.js" v-model="blog.categories">
        <label>React.js</label>
        <input type="checkbox" value="React.js" v-model="blog.categories">
        <label>Angular.js</label>
        <input type="checkbox" value="Angular.js" v-model="blog.categories">
      </div>

      <!--    作者模块-->
      <label>作者</label>
      <select v-model="author">
        <option v-for="(item, i) in authors" :key="i">{{item}}</option>
      </select>

      <button @click.prevent="post">添加博客</button>
    </form>

    <div v-if="!submmited">
      <h3>您的博客发布成功</h3>
    </div>

    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题: {{ blog.title }}</p>
      <p>博客内容:</p>
      <p>{{blog.content}}</p>
      <p>博客分类</p>
      <ul>
        <li v-for="(category, i) in blog.categories" :key="i">
          {{category}}
        </li>
      </ul>
      <p>博客作者:{{author}}</p>
    </div>
<!--    <button @click="get">获取数据</button>-->
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'add-blog',
  data() {
    return {
      blog: {
        title: '',
        content: '',
        categories: []
      },
      authors: ['Yefeng', 'Let'],
      author: '',
      submmited: true
    }
  },
  methods: {
    post() {
      axios.post('https://vue-demo-774d0-default-rtdb.firebaseio.com/posts.json',
        { title: this.blog.title, body: this.blog.content, username: this.author }).then((res) => {
        console.log(res)
        this.submmited = false
      })
    },
    // async get() {
    //   const res = await axios.get('/posts')
    //   console.log(res.data)
    //   this.submmited = false
    // },
    created() {

    }
  }
}
</script>

