<template>
  <div v-theme="'narrow'" id="show-blogs">
    <h1>Show - Blogs</h1>
    <input type="search" v-model="search" placeholder="搜索">
    <div v-for="(item, i) in filteredBlogs" :key="i" class="single-blog">
      <router-link :to="'/blog/' + item.id">
        <h2 v-rainbow>{{ item.title }}</h2>
      </router-link>
      <article>{{ item.body }}</article>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'show-blogs',
  data() {
    return {
      blogs: [],
      search: ''
    }
  },
  created() {
    axios.get('https://vue-demo-774d0-default-rtdb.firebaseio.com/posts.json')
      .then((res) => {
        // this.blogs = res
        // console.log(res.data)

        const blogsArray = []
        for (const key in res.data) {
          // console.log(Key)
          res.data[key].id = key
          blogsArray.push(res.data[key])
        }
        // console.log(blogsArray)
        // console.log(res.data)
        this.blogs = blogsArray
      })
  },
  methods: {
  },
  // 计算方法
  computed: {
    // 筛选出搜索的
    filteredBlogs() {
      return this.blogs.filter((blog) => {
        return blog.title.match(this.search)
      })
    }
  },
  // 私有 过滤器
  filters: {
  },
  // 私有 自定义指令
  directives: {

  }
}
</script>

<style lang="less">
#show-blogs{
  max-width: 800px;
  margin: 0 auto;

  a{
    text-decoration: none;
  }

  input[type="search"]{
    padding: 8px;
    width: 100%;
    box-sizing: border-box;
    border: 1px dotted #aaa;
    border-radius: 5px;
    outline: none;
  }
}
.single-blog {
  padding: 20px;
  margin: 20px 0;
  box-sizing: border-box;
  background: #eee;
  border-radius: 10px;
  border: 1px dotted #aaa;
}

</style>
