<template>
  <div id="single-blog">
    <h1>博客号：{{id}}</h1>
    <h1>{{blog.title}}</h1>
    <article>{{blog.body}}</article>
    <p>作者:{{blog.username}}</p>
    <p>分类：</p>
    <ul>
      <li v-for="(category, i) in blog.categories" :key="i">
        {{category}}
      </li>
    </ul>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'single-blog',
  data() {
    return {
      id: this.$route.params.id,
      blog: {}
    }
  },
  created() {
    axios.get('https://vue-demo-774d0-default-rtdb.firebaseio.com/posts/' + this.id + '.json')
      .then((res) => {
        this.blog = res.data
        // console.log(this.blog)
      })
  }

}
</script>

<style>
#single-blog{
  max-width: 960px;
  margin: 0 auto;
  padding: 20px;
  background: #eee;
  border: 1px dotted #aaa;
  border-radius: 10px;
}
</style>
